<template>
  <div>
    <header>
      <img src="../../assets/home/fanhui.png" alt="" class="goback" @click="goback">
      <div class="title"></div>
      <div class="release"></div>
    </header>
    <!-- <div class="title"></div> -->
    <div class="agreement collect_content-html" v-html="pagedata.content">
<!-- 
      1.

      1.1/

      1.2

      2.

      2.1

      2.2

      2.3 
       2.4 ()()

      3.

      3.1 

      3.1.116(8)

      3.1.214(7)147

      3.2

      

       -->

    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data(){
    return {
      pagedata:{}
    }
  },
  mounted(){
    this.getpageData();
  },
  methods:{
    async getpageData(){
      let data = {}
      try{
        let result = await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Banner&a=agreement",qs.stringify(data))
        
        if(result.data.status==1){
          
          this.pagedata = result.data.data
        }
      }catch(err){
        console.log(err);
      }
    },
    goback(){
      window.history.back(-1);
    }
  }
}
</script>
<style scoped>
  .title{
    color: #333333;
  }
  .agreement{
    font-size: 0.8rem;
    padding: 0.5rem 0.7rem;
    line-height: 1.5rem;
    text-align: left;
    text-indent: 1.5rem;
  }
</style>
<style scoped>
  header{
    background: #00BFFE;
    padding: 1rem;
    display: flex;
    font-weight: bold;
    justify-content: space-between;
    /* border: 1px solid #f00; */
    height: 2.4rem;
    line-height: 3.4rem;
    color: #ffffff;
  }
  .goback{
    width: 0.5rem;
    height: 1rem;
    color: #D8F2FE;
    padding-top: 1.2rem;
  }
  .title{
    font-size: 1.1rem;
    letter-spacing: 0.2rem;
    color: #EDFAFE;
    font-family: '';
  }
  .release{
    width: 0.5rem;
    height: 0.5rem;
  }
  .collect_content{
    padding: 1rem;
    font-size: 0.9rem;
    text-align: left;
    color: #333;
    line-height: 1.1rem;
  }
  .collect_content p{
    margin-bottom: 1rem;
  }
  .collect_content p.bold{
    font-weight: bold;
    margin-bottom: 0.5rem;
  }

</style>
<style>
.collect_content-html{
    padding: 1rem;
    font-size: 0.9rem;
    text-align: left;
    color: #333;
    line-height: 1.1rem;
  }
</style>


